.main {
  width: 100%;
  height: 100%;
}

.topbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin: 20px;
}

.mainTitle {
  width: 100%;
  font-size: 50pt;
  font-weight: bold;
  text-shadow: 0px 1px 2px #888;
  padding-left: 100px;
}

.clock {
  width: 700px;
  height: 60px;
  font-size: 30pt;
  color: #fff;
  text-shadow: 0px 1px 2px #888;
  /* padding-left: 100px; */
  /* background-color: #374f50; */
  text-align: right;
  padding-right: 50px;
  padding-top: 45px;
}

.tbTitle {
  width: 100%;
  height: 100px;
  color: #fff;
  font-size: 40pt;
  text-shadow: 0px 1px 2px #888;
  /* display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; */

  background: #77b1b3;
  /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left, #374f50, #63a6a9, #374f50);
  /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(right, #374f50, #63a6a9, #374f50);
  /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(right, #374f50, #63a6a9, #374f50);
  /* For Firefox 3.6 to 15 */
  background: linear-gradient(to right, #374f50, #63a6a9, #374f50);
  /* Standard syntax */
}

